body {
    background: #ffffff;
    color: #000000;
}

* {
    margin: 0;
    padding: 0;
}

.card {
    width: 100vw;
    border: none;
    margin: 2vh auto;
}

.active:hover {
    color: #ffe331 !important;
    text-decoration: underline;
    text-decoration-thickness: 0.15rem;
    text-shadow:#f4d403 !important ;
}

.navbg {
    background-color: #000000 !important;
}

.sticKey-nav {
    position: sticky;
    top: 0vh;
    z-index: 1;
}

.search {
    width: 15rem;
}

.color-light {
    color: aliceblue;
}

.btn-link {
    list-style: none;
    text-decoration: none;
}

.invert {
    filter: invert(100%);
    width: 3rem;
    height: 2rem;
    margin: 0;
}

.card-body {
    background: #000000;
    color: #ffffff;
}

.card-body-2 {
    width: max-content;
    min-width: 100%;
}

.cards {
    width: 50rem;
    margin: 4vh auto;
    list-style: none;
}

.toolbar-item button {
    cursor: pointer !important;
    color: rgb(0, 255, 255) !important;
    border: 0.5px solid rgb(71, 254, 4) !important;
    padding: 4px !important;
}

.ul1 {
    margin: 0;
    padding: 0;
}

.nav-link {
    margin: 0;
}

.lang-img {
    background: #000;
    max-width: 16vw;
    max-height: 16vh;
}

.img-container {
    width: 16vw;
    height: 16vh;
    display: flex;
    align-items: center;
    justify-content: center;
    height: fit-content;
}

.row-container {
    background: #000;
}
.toggle-img{
    height: 1.5rem;
}
@media only screen and (max-width: 854px) {
    .cards {
        width: 90vw;
        margin: 2vh auto;
        list-style: none;
    }

    .search {
        width: 90vw;
    }
}
.card-dark {
    background: #000;
    color: white;
    display: flex;
}

.language-logo {
    width: 10em;
    height: fit-content;
    padding: 1rem;
}

.topics li {
    list-style: decimal;
}
.language-git{
    color: rgba(0, 255, 0, 0.814) !important;
}