.custom-btn {
    color: #fff;
    border-radius: 5px;
    padding: 0.4rem;
    font-family: 'Lato', sans-serif;
    /* font-size: smaller; */
    background: transparent;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    display: inline-block;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
    outline: none;
    margin: 0.3rem;
}


.btn-14 {
    background: rgb(0, 47, 255);
    border: none;
    z-index: 1;
}

.btn-14:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 0;
    top: 0;
    left: 0;
    z-index: -1;
    border-radius: 5px;
    background-color: #00fff2;
    box-shadow: inset 2px 2px 2px 0px rgba(255, 255, 255, .5),
        7px 7px 20px 0px rgba(0, 0, 0, .1),
        4px 4px 5px 0px rgba(0, 0, 0, .1);
    transition: all 0.3s ease;
    filter: drop-shadow(4px 10px 6px rgb(0, 251, 255));
}

.btn-14:hover {
    color: #000;
}

.btn-14:hover:after {
    top: auto;
    bottom: 0;
    height: 100%;
}

.btn-14:active {
    top: 2px;
}

.topic {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height:fit-content;
    overflow-y: scroll;
    background: white;
    color: #000;
    position: sticky;
    left: 75vw;
}

.h1 {
    margin: 1.3vh 2vw;
}